import React from 'react';
import { Blog } from '../../components';

export default function raffy_halim() {
    return (
        <Blog.TextBlock>
            <Blog.Author>Interviewed by Jefferson Tang</Blog.Author>
            <Blog.Author>Written by Nada Haridy</Blog.Author>
            <br/>
            <br/>
            <Blog.Text><i>Raffy Halim is a specialist anesthetist based at Western Health and a multimedia editor for the Journal of Mobile Technology in Medicine (JMTM). Apart from medicine, photography, web developing, and technology are a few interests that take up his time.  </i></Blog.Text>
            <br/>
            <br/>
            <Blog.Subtitle>GETTING INTO PHOTOGRAPHY</Blog.Subtitle>
            <Blog.Text>Around the time I was in high school, there were a couple of amazing websites that did cool stuff with flash and thats what got me into it. I tried taking pictures and then using them to come up with web designs. It stayed a hobby of mine on the side, until sometime around medical school, someone showed me a DSLR camera. I played around with the settings and it took on from there.</Blog.Text>
            <Blog.Text>Because of this background, a lot of my non-clinical time now is spent taking photos, filming and creating VR content for the anaesthesia department, like staff portraits and VR simulations. (See https://youtu.be/-mcuoafxh5c for an example).</Blog.Text>
            <br/>
            <br/>
            <Blog.Subtitle>INTEREST IN CRITICAL CARE</Blog.Subtitle>
            <Blog.Text>Initially I wanted to be a cardiologist, but I quickly realised that I wasnt cut out to be a physician because I didnt enjoy ward rounds or clinics. Back then, as a student, we didnt get proper exposure to anaesthetics, so I travelled to Malaysia. I remember sitting down in the tea room with 3 consultants and then suddenly, the emergency buzzer went off, so they ran over to the patient who was a neonate. They were so slick; one handled the airways, the other got a drip in, and the third one was doing something else. Five minutes later, the baby was fine and they went back to the tea room and carried on with the conversation that they were having. It seemed like there was a break in reality where they paused to do something cool, then carried on. That was the day I decided I wanted to be an anaesthetist.</Blog.Text>
            <br/>
            <br/>
            <Blog.Subtitle>THE BEGINNING OF THE JOURNAL OF MOBILE TECHNOLOGY IN MEDICINE</Blog.Subtitle>
            <Blog.Text>One of my good friends, Chandra, was having trouble submitting to journals. Him and another friend, Rahul, started thinking why dont we start our own journal for health and tech since we liked both. Then 5-6 of us had dinner together and decided to give it a go and we did. Its now been 6 years!</Blog.Text>
            <br/>
            <br/>
            <Blog.Subtitle>MEDICAL EXPERIENCES THAT STAND OUT </Blog.Subtitle>
            <Blog.Text>Internship was an interesting experience. I was in Bairnsdale, and being a keen intern, I showed up to ED early before orientation had started. They told me to try picking up a patient just to get used to the paperwork since I was new. So I went to see a patient who had abdominal pain and was nauseated. I couldnt even remember the names of maxolon and ondansetron. I remember telling the nurse to give the drug that sounded like a transformer robot, so she said, ondansetron? I was like yes! Then that didnt work, so I told her to give her its arch nemesis, meaning maxolon. As I figured out the dose, the patient arrested and died. That was the beginning of my medical career. There was dead silence for the rest of the orientation. By the end of the day, I was getting texts from unknown numbers in Swan Hill asking me if I was okay, and I was like, who are you?.</Blog.Text>
            <br/>
            <br/>
            <Blog.Subtitle>A MESSAGE TO MEDICAL STUDENTS AND JUNIOR DOCTORS</Blog.Subtitle>
            <Blog.Text>One of the benefits of medicine is that you dont ever get an existential crisis about whether or not what you're doing is worthwhile. Youre always helping people, although we undersell that element now. You should keep in mind that what youre doing has real human benefit, otherwise its easy to get jaded with exams and tough rotations when they happen. The other thing I would recommend is to try and not let medicine be everything in your life. There was a TED talk that explained how this generation was not expected to have just one career for their entire life. You change about 6 times, but instead of doing them all at the same time, do them in series, not in parallel. Keep that in mind especially when you start to get more opportunities later in your career, youll want to do them all but its ok to pick just one.</Blog.Text>
            <br/>
            <br/>
            <Blog.Subtitle>BILLBOARD MESSAGE</Blog.Subtitle>
            <Blog.Text>Id say remember that youre mortal. It might be the opposite of what I was saying earlier, but dont procrastinate. Theres a balance between being too relaxed and too intense, and you should always try to have a middle ground. The other thing is that we forget is that time is limited, so not only go do stuff, but also appreciate the little moments, whether thats coffee, your family or friends. Remember that these things wont always last and that you wont always be around. Also, in the cosmic scale of things, none of what youre doing is epically important, so dont take yourself too seriously!</Blog.Text>
            <br/>
            <br/>
        </Blog.TextBlock>
    )
}