import React from 'react';
import { Blog } from '../../components';

export default function exam_edition_2() {
    return (
        <Blog.TextBlock>
            <Blog.Author>Interviewed and written by Jefferson Tang</Blog.Author>
            <br/>
            <br/>
            <Blog.Text>Meet Emily Warton and Daniel Hynes, two friendly Victorian doctors who have recently graduated from Monash University and the University of Melbourne respectively . Emily is currently a medical intern with Monash Health, and <b>DANIEL:</b> is a general medicine registrar with Eastern Health. They have gathered to share their best tips on how to tackle the impending challenges of the end-of-year medical school written exams and OSCEs, and engage in some wholesome banter along the way </Blog.Text>
            <br/>
            <br/>
            <Blog.Subtitle>6 What can you be proud of the younger medical student self for, and what foolishness would you smack your younger selfs head for?</Blog.Subtitle>
            <Blog.Text><b>EMILY:</b> I wish I went to Maccas less. Im sympathetic to the forces that were pulling me towards Maccas and away from studying. We would regularly drive out of town 15-minutes away from the student accommodation to visit the local Maccas, actually one time we visited that Maccas three times in one day. I regret doing that. But I did start an exercise regime for the first time in my life that year, which was good. I could give myself a gold star for that.</Blog.Text>
            <Blog.Text>I wish I studied more, especially in the years where people said, this year doesnt count. If you treat those years like they dont count, you then have a shaky foundation for the following year. You should just study all the way through.</Blog.Text>
            <Blog.Text><b>DANIEL:</b>: I guess its always easier to find things to criticise yourself for, rather than things to pat yourself on the back for. Im struggling to think of anything to pat myself on the back for. I guess I did a good job turning up to lectures.</Blog.Text>
            <Blog.Text>General advice would be, dont treat med school like its some finite thing where youre going to get to the end of it, stop studying, and everything will be fine and rosy. Now is the time to start building those study habits, because youre going to be studying for the rest of your medical career.</Blog.Text>
            <Blog.Text>A good habit to get into is to regularly sit down after a ward round and go these are the things I didnt know, Im going to go and look them up. Im not going to do it tomorrow. Im going to do it tonight and Im going to make sure I understand it. Then tomorrow Ill look back and double check that I remember it.</Blog.Text>
            <Blog.Text>I think the big trick to winning in med school and beyond is just being time effective. Ideally you would regiment time for study, time for exercise, time for extracurricular activities - it can be volunteering for some outreach group, or being a research assistant, anything youre interested in and can fit time for. You can also fit in time to get really hammered or whatever floats your boat.</Blog.Text>
            <Blog.Text>If you start budgeting your time now, youll find that when things get busy youve already got those habits on autopilot. Because if you play your cards well, you can come out of medical school with a fantastic hand: some useful connections with senior clinicians, a few research papers in the pipeline, and a start on something exciting like a podcast. Or you could study really hard, get an amazing Z score that becomes redundant as soon as you get your intern job, and have a blank CV. Those are the two different directions in which you can go.</Blog.Text>
            <Blog.Text>My advice is, dont do what <b>DANIEL:</b> did which was get an average score and nothing on the CV. Try and spread yourself around a little bit, and be organised.</Blog.Text>
            <br/>
            <br/>
            <Blog.Subtitle>7 How do you deal with working full-time and studying for exams at the same time?</Blog.Subtitle>
            <Blog.Text><b>DANIEL:</b>: You have got to actually get joy out of learning new and interesting things all the time, and you have to fit the rest of your life around it. Even once you get to the other side of your current exams, it doesnt stop. Its not like you are finished with your exams once and for all, and the medicine you need to learn never progresses beyond there. There are always new things that you have to learn and know, and you always have to allocate a bit of of your personal time to study, as well as to other medical things, and of course to your friends, family and own wellbeing. None of that really changes throughout your career, maybe just the relative ratios.</Blog.Text>
            <Blog.Text>This will surprise you, but medical school is probably the least busy you will be throughout your career. If you can smash out some of the things now that you would have to do later on in your career anyway, then you're playing the system very well. Because you can either work on those research projects now, or you can work on them while youre doing 50 hour work-weeks.</Blog.Text>
            <br/>
            <br/>
            <Blog.Subtitle>8 How do you take care of your health and relationships during the exam lockdown period, and also in times when you have lot of things on your plate?</Blog.Subtitle>
            <Blog.Text><b>EMILY:</b> Dont take out your stress on anyone else.</Blog.Text>
            <Blog.Text><b>DANIEL:</b>: This is where time budgeting is important. Putting yourself into an absolute lockdown mode and never seeing your friends, family or loved one is not healthy. You will actually do worse in the long run if you do that. You need a break every so often. If you find that you keep pausing to play Dota (or some other video game or procrastination activity), it means that youre a bit cooked (from all the study) and maybe you need to study a bit less and spend a bit more time with your loved ones. Make your study time count and make your spare time count.</Blog.Text>
            <Blog.Text>Dont lose your temper at people you care about if youre having a bad time from the exam study. Bite your tongue for a second, take a deep breath, and tell them sorry Im very stressed and these are the reasons.</Blog.Text>
            <Blog.Text><b>EMILY:</b> People will understand. Youre doing medical school and youre going through some intense exams. They will understand that you wont be present very much. Just dont get carried away with the momentum of how stressed you are and let it negatively impact your relationships. Always feel free to share your feelings with other people because thats the best thing you can do.</Blog.Text>
            <Blog.Text><b>DANIEL:</b>: It took me a long time to actually believe that regular exercise and keeping fit will help you be much more sharp on the actual exam day. It also makes a massive difference to how effectively you study. Its very tempting to say no, Ill put in my five hours of study and the length of time I spend studying is what matters. But in reality, if you go for a run and then sit down to study afterwards, youll make the hour-long (or however long) study session count for so much more. The mental crispness you get just after exercise helps you make leaps in your exam study journey, whereas youll be like a cripple hobbling on crutches on your exam study journey without regular exercise.</Blog.Text>
            <Blog.Text>The exams never stop and everything only gets harder once you start working. Youll burn out quickly if you sacrifice your health and relationships for the sake of exam study.</Blog.Text>
            <br/>
            <br/>
            <Blog.Subtitle>9 If your results end up being worse than anticipated, or if you end up failing, what is the best way to cope and respond?</Blog.Subtitle>
            <Blog.Text><b>EMILY:</b> I think talking to someone about it is the starting point. Trying to figure out if you fail, how can you sort it out and move forward with your goals. Hopefully the university is supportive of your situation and theyll give you the information early on. Its quite structured and you dont have to fly around trying to figure out things for yourself. And try not to take it too personally.</Blog.Text>
            <Blog.Text><b>DANIEL:</b>: Everyone always stresses about failing uni exams. However there is a strong financial incentive for the university to pass you. You are probably going to end up all right.</Blog.Text>
            <Blog.Text>In the off chance that you fail, its a game. Dont take it personally because there is no such thing as a perfect exam where you are fairly assessed on how good you are as a medical student or as a doctor or whatever.</Blog.Text>
            <Blog.Text>Have people you can talk to about it; people that you can trust.</Blog.Text>
            <Blog.Text>You can go one of two ways after you suffer a exam failure: you can either go on a big negative spiral of Ive had a shit time so Im going to get really blind drunk for a while and Im not going to do much. Maybe Ill tell my girlfriend that she ruined everything and its her fault. Or you can go the other way and say, All right, I failed and I feel really inadequate about how good I am. So Im going to study even harder, Im going to go for that run, Im going to eat really well and Im going to sleep as well as I can. The only way to get over feeling inadequate about failing an exam is to do some things that make you feel like youre a bit of a winner.</Blog.Text>
            <Blog.Text><b>EMILY:</b> People fail all the time in every domain of life. Especially in medical school when youve got these exams, you just focus on the next thing in front of you and youre not seeing the hundreds of things behind you. Everyone around you has failed in multiple things.</Blog.Text>
            <Blog.Text>Your seniors have failed. If you fail now, it will mean that you take something from the experience and you wont fail in the future where your peers will.</Blog.Text>
            <Blog.Text><b>DANIEL:</b>: Its character building. In the b=ig scheme of things, even if youre an old Melbourne postgrad, I promise that by the time youre a consultant, youll still be super young if you just blast through (the whole training path) without ever stopping. Once you hit that point, theres nowhere else to go. So dont rush. Take your time. Learn what you need to learn, make the mistakes you need to make and learn from them. Dont feel too bad about it, its all a part of life.</Blog.Text>
            <Blog.Text><b>EMILY:</b> I think the hard and scary thing for a lot of people is other people finding out that they failed. Your one exam failure doesnt reflect on you as a person. I think its all about keeping perspective that people fail all the time and this is just a single event. In the future, if one of your peers fails, they will hold themselves completely accountable for it. They wont be thinking of your failure, they will instead blow their own failure out of proportions. Your failure is just a blip in everyone elses mind, and is really massive to you. Dont worry too much about what other people are thinking about you, because its always so much more important to you than it is to them.</Blog.Text>
            <Blog.Text><b>DANIEL:</b>: Its super true, were all terrible at dealing with failure. We all define our ideas of whether we are successful people or not a bit too much by how we are doing in medicine, which is not entirely valid. You need to keep in mind that although you might not have passed an exam, you need to look at the broader picture and think about all the other things in life that youre winning at.</Blog.Text>
            <br/>
            <br/>
            <Blog.Subtitle> What pleasures can you allow yourself during the exam lockdown period?</Blog.Subtitle>
            <Blog.Text><b>DANIEL:</b>: Enjoy the things that are good for you as much as you can. Enjoy delicious, succulent meals. Enjoy sleeping, and enjoy exercise - actually enjoy it, it shouldnt be a chore.</Blog.Text>
            <Blog.Text>You can still allow yourself some time to have fun hanging out with your mates. Again, do everything in moderation. Just make sure youre organising your time well so that you can fit all those things in. Obviously, dont get completely hammered the night before your exams.</Blog.Text>
            <Blog.Text>Just nothing in excess really is the key. You can allow yourself lots of pleasure, just not in excess.</Blog.Text>
            <br/>
            <br/>
        </Blog.TextBlock>
    )
}