import React from 'react';
import { Blog } from '../../components';

export default function sally_graddon() {
    return (
        <Blog.TextBlock>
            <Blog.Author>Written by Erica Musgrove</Blog.Author>
            <br />
            <br />
            <Blog.Subtitle>Doctor to life coach</Blog.Subtitle>
            <Blog.Text>I always wanted to be a doctor, from the age of 2. Its one of those things where if you tell somebody, you get a really positive response. I never allowed myself to explore anything else. My identity had been so tied up as being a doctor, there was nothing else left of me. Eventually I came across meditation and mindfulness. That was my first step to working out who I was and what I wanted</Blog.Text>
            <Blog.Text>I watched a video one day and I just knew in that instant that life coaching was the path for me and doctors were the people that I needed to help. Everybody has the answers inside of them. Its about asking the right questions and holding up a mirror - thats coaching. Its watching the light go back on in their eyes. That complete change from seeing no alternatives to being really excited about what theyre doing is incredible</Blog.Text>
            <br />
            <br />
            <Blog.Subtitle>Leaving medicine</Blog.Subtitle>
            <Blog.Text>There are people who always wanted to be a doctor and were really passionate, and have had that passion eroded over time, then there are those that fell into it. People have simply lost touch with who they are.</Blog.Text>
            <Blog.Text>Theres so much stigma surrounding leaving medicine; unless youve been a doctor and left, you really dont understand the issues that are involved, the emotion, and the grieving process that you need to go through. Theres so much tied up in training to be a doctor, and then theres the feeling that youre throwing it all away</Blog.Text>
            <br />
            <br />
            <Blog.Subtitle>Thriving in medicine</Blog.Subtitle>
            <Blog.Text>Doctors feel as though theyre not allowed to bring spirituality into medicine. But we all know that patients have much better outcomes when they feel heard and cared for - thats healing. The more people who come into medicine more in touch with that really nurturing side of themselves, thats when medicine itself is going to start to heal</Blog.Text>
            <Blog.Text>Medicine is an important part of your life, but you are not medicine, it is just a part of your story. Its about not allowing yourself to get so bogged down with life that you forget to live it. Be true to yourself. Believe that anything is possible. If you have the tenacity, strength, intelligence, and bravery to become a doctor, you can literally do anything!</Blog.Text>
            <br />
            <br />
        </Blog.TextBlock>
    )
}