import React from 'react';
import { Blog } from '../../components';

export default function exam_edition_1  () {
    return (
        <Blog.TextBlock>
            <Blog.Author>Interviewed and written by Jefferson Tang</Blog.Author>
            <br/>
            <br/>
            <Blog.Text>Meet Emily Warton and Daniel Hynes, two friendly Victorian doctors who have recently graduated from Monash University and the University of Melbourne respectively . Emily is currently a medical intern with Monash Health, and Daniel is a general medicine registrar with Eastern Health. They have gathered to share their best tips on how to tackle the impending challenges of end-of-year medical school written exams and OSCEs, and engage in some wholesome banter along the way </Blog.Text>
            <br/>
            <br/>
            <Blog.Subtitle>1 What was the best exam result you have achieved since starting medical school, and what do you think contributed to that result?</Blog.Subtitle>
            <Blog.Text><b>DANIEL:</b> Probably my MD2 exam was my best result. What worked well for me was that I had a good study group  we all just smashed our exam study hard every day and it didnt feel like as much of a chore when you did it together with a whole bunch of people. We had a vibe where everyone pushed one another, and we connected well enough that we didnt want to kill each other while studying together. Being a rural cohort removed from the rest of Melbourne, we had an underdog mentality and felt that we had to push really, really hard. It was a pleasant surprise when we found that we had all nailed the exams. </Blog.Text>
            <Blog.Text>It was the year where Melbourne put up a curriculum of things you needed to know, so I just picked up every condition on the curriculum and learned everything I could on each of those conditions  radiology, pathophysiology, history, examination findings, investigations, management, everything. When I got to the exams it all fell into place.</Blog.Text>
            <Blog.Text><b>EMILY:</b> I guess the best result from studying really hard for exams isnt the exam result itself; its the knowledge you have gained that you can now use in your work and life. </Blog.Text>
            <Blog.Text><b>DANIEL:</b> You dont think about it this way when youre a medical student, but your (medical student exam) scores are like VCE: the (exam) scores get you into an internship somewhere and it then becomes irrelevant for the rest of your life. What matters in the end is the knowledge you have gained that you need as a doctor when you are managing patients and their illnesses. Of course there are always more irrelevant things that the uni makes you learn - pathology for example - that you put in your brain just for the exam. Afterwards you can use whatever brand of whiskey you need to get that out of your brain. </Blog.Text>
            <br/>
            <br/>
            <Blog.Subtitle>2 If you had OSCEs in a months time, how would you prepare for them?</Blog.Subtitle>
            <Blog.Text><b>EMILY:</b> I would recruit other people who are hopefully going through the same thing and (in a friendly, congenial manner) bully them into joining my study schedule religiously. Then we would all compile OSCE stations - its good to have a rich flow of information between everyone. I guess most universities will have a pool of previous exams and resources; get your hands on those and dont be sitting outside that pool.</Blog.Text>
            <Blog.Text>Go through the resources and set up proper OSCE stations. Dont laugh through them. Act like its the real thing. Its helpful to have an adjudicator to keep time when theres an odd person out. Get the flow of the each clinical examination right because thats the most difficult and useful thing to master.</Blog.Text>
            <Blog.Text><b>DANIEL:</b> Everything is much better as a group. I lived in dorms and so we would just come into each others rooms after lectures were done for the day. Wed pick a system - for example cardiovascular - and we would keep going at it until we got it down to muscle memory. Someone would throw signs at you, and you would eventually get good at putting the pieces together while doing the exam and make a reasonable diagnosis at the end.</Blog.Text>
            <Blog.Text>I mean OSCEs arent that hard. The bit that is tricky is not falling to bits with nerves on the day. The key to that is just muscle memory. All of your exams have to be so slick that when you are really distracted and sweaty and sleep deprived because you couldnt sleep at all the night before and didnt have breakfast that morning, you can just into autopilot. All youre doing on the day is putting the signs together while youre in autopilot.</Blog.Text>
            <Blog.Text>Also remember that common is common, especially at the medical student level. If you get asked to do a respiratory exam, itll probably be COPD and not something too weird and wonderful. Just remember the common things well.</Blog.Text>
            <Blog.Text>The best thing you can do for OSCES is to get into the hospital, peel off after rounds, and find and examine all the patients with signs. Otherwise you will get out of medical school and be hearing wheeze for the first time. You dont want to be in that spot.</Blog.Text>
            <br/>
            <br/>
            <Blog.Subtitle>3 How would you prepare for a set of written exams?</Blog.Subtitle>
            <Blog.Text><b>DANIEL:</b> You can get away with a bit more solo time with written exams, whereas for OSCEs you really want to do it with someone who can give you direct feedback. Its really good to do it in groups of three where you rotate roles and learn from all three perspectives. For written exams its great having a study group for the motivational vibes and picking up little bits of knowledge that you didnt realise you were lacking.</Blog.Text>
            <Blog.Text><b>EMILY:</b> Also with written exams, if you get something wrong you can make a little database of all the questions you get wrong and go through them again at a later stage as as a separate question bank. You know then that you can get them right if they come up on the actual written exam.</Blog.Text>
            <br/>
            <br/>
            <Blog.Subtitle>4 How do you deal with on-the-day exam nerves?</Blog.Subtitle>
            <Blog.Text><b>EMILY:</b> You just go into autopilot. Theres not much you can do once you get in whatever mental zone you get into on the day.</Blog.Text>
            <Blog.Text><b>DANIEL:</b> Best thing to do, especially if theres a practical component, is to train it into autopilot as much as you can. But if you can, eat a good breakfast and get a good nights sleep beforehand.</Blog.Text>
            <Blog.Text>Also dont forget to exercise. Everyone always said that to me as a student, and I would just say, Yeah, great point, but I need to study. But it actually makes a really big difference to your concentration levels: both when youre studying, and on the day. If you're physically fit youre going to be a lot more confident. </Blog.Text>
            <Blog.Text><b>EMILY:</b> Dont psych yourself out too much. Dont put the blinkers on. You can always remove yourself from the situation. This is just for one or two hours, and then Im going to be out in the sun for whatever. </Blog.Text>
            <Blog.Text><b>JEFFERSON:</b> Ill add another tip - make sure your head is completely clear coming into the exams. What I always used to do before exams was to take time time right before the exam start time to do a breathing exercise. I would count to maybe 100 breaths, so that I could clear my mind of any distracting thoughts. That way youre completely calm and focused, and 100% in the moment for the entire duration of the actual exam.</Blog.Text>
            <Blog.Text>The second thing that has worked with me is to visualise yourself succeeding at the exam for the entire month leading up to the exam. Olympic performers do the same thing - Ive read that gold medalists dream about and visualise themselves winning the gold medal for years before they went and did it. You can apply the same practice to exams. Ive found that persistent visualisation makes a noticeable difference in your confidence levels, and drives you to make the correct day-to-day decisions (e.g. get proper sleep, not procrastinate, eat well, etc) that lead to exam success.</Blog.Text>
            <Blog.Text><b>DANIEL:</b> Fair enough. Relaxation techniques, mindfulness, all that stuff is pretty handy. Even during the exams just be mindful of your stress levels and if you feel that youre going off the rails, it might be worth that 30 seconds out of your writing time to just stop, take a few deep breaths, destress a little, and then get back into it.</Blog.Text>
            <Blog.Text>When youre going between OSCE stations, dont take the last OSCE stations memories over to the next station. Leave it behind and move on with a clean, crisp and fresh mind.</Blog.Text>
            <Blog.Text><b>JEFFERSON:</b> Its okay if things dont go perfectly according to plan. Most likely theyre not, and youll have questions that throw you off on initial glance. But you have got to have that confidence that no matter what happens, you can make it work. </Blog.Text>
            <br/>
            <br/>
            <Blog.Subtitle>5 What has been your most memorable exam experience?</Blog.Subtitle>
            <Blog.Text><b>EMILY:</b> My most memorable exam memory was right before our VIA which is the most important exam in our entire curriculum. One of my friends got stung in the face by a bee . The poor girl. It got her right between the eyes and her whole face became completely swollen up.  But she just went straight in (to the exam hall), smashed that exam like an absolute champion, and came out. It was crazy.</Blog.Text>
            <br/>
            <br/>
        </Blog.TextBlock>
    )
}