import React from 'react';
import { Blog } from '../../components';

export default function bradley_frew() {
    return (
        <Blog.TextBlock>
            <Blog.Author>Interviewed and written by Jessie Zhou</Blog.Author>
            <br />
            <br />
            <Blog.Text><i>Dr Bradley Frew is an Academic General Practitioner based in the Peninsula Area in Melbourne, Victoria who is also a lecturer within the Department of General Practice, Monash University. His areas of interest within General Practice include: diving and aviation medicine, mental health and adolescent/young adult medicine, as well as medical education. In a previous life Brad was a Lieutenant Medical Officer for the Royal Australian Navy.</i></Blog.Text>
            <br />
            <br />
            <Blog.Subtitle>MEDICINE IN THE MILITARY</Blog.Subtitle>
            <br />
            <br />
            <Blog.Text><b>Never been fitter life as a Lieutenant Medical Officer for the Royal Australian Navy</b></Blog.Text>
            <Blog.Text> 00:36</Blog.Text>
            <Blog.Text>I was approached about taking up one of the military scholarships during my third year of medical school. As part of that process, you get paid a salary, rent allowance and get textbooks covered - it's quite a good deal! In return, you have to commit to military service for a specified period. I took the scholarship for two-and-a-half years (through the fourth, fifth, and sixth years of my medical school training) and then completed my intern and resident years at Monash Health.</Blog.Text>
            <Blog.Text>After that I was a full-time medical officer for the Navy. The first six months in the Navy was officer training doing the yes sir, no sir, three bags full sir, lots of pushups (never been fitter), 5:30 starts and all the traditional military activities(zooming around in boats, shooting raffles, blowing up stuff) - which was entirely non-medical!</Blog.Text>
            <Blog.Text>After that there was medical work: the bulk of my military medical work was being involved inOperation Resolute, which involved border patrol and providing humanitarian aid to the refugees that came over by boat. We would intercept the refugees during some pretty horrific circumstances, and serve some very unwell people.  </Blog.Text>
            <Blog.Text>In these austere environments,you have to think on your feet: boats are rocking around, the place is full of diesel fumes and you have people coming in with large lacerations, pregnancy and chronic illnesses - all requiring medical care.</Blog.Text>
            <Blog.Text>You have to patch them up the best you can, before they get transferred to various processing facilities. I thought it was horrific that we're sending them to detention centers on islands, because it's just not the way you treat refugees, but you can at least try and provide a little bit of comfort and be a smiling face for them. </Blog.Text>
            <Blog.Text>I had a few other overseas deployments which were a bit more fun. I went over to Hawaii as we tested some missile systems and did some stuff with the US and then did a deployment to China - a diplomatic one - where we were celebrating People's Liberation(Navys 60th anniversary) and I got to play around with the Russians and all the other navies from around the globe.</Blog.Text>
            <br />
            <br />
            <Blog.Text><b>Staying in the military is a tricky question for a lot of doctors</b></Blog.Text>
            <Blog.Text><b> 04:04</b></Blog.Text>
            <Blog.Text>The longer you stay in the military, the less clinical you become and your role becomes more of a medical administrator, which was not a career path I was interested in. There's also a lot of time away. By that stage I found my partner and the concept of spending up to 12 months away on deployment was a tough ask. So it became pretty obvious that I didn't really want to stay in the military. But I got some great training while I was there, including diving and aviation medicine, which are transferable clinical skills to the civilian world.</Blog.Text>
            <br />
            <br />
            <Blog.Text><b>Advice for medical students and doctors considering the military as a career</b></Blog.Text>
            <Blog.Text><b> 06:59</b></Blog.Text>
            <Blog.Text>The bottom line for anyone thinking about the military is if you want to work full time for the military, then general practice is your only option.</Blog.Text>
            <Blog.Text>Recruiting will probably tell you: Oh, you can be an orthopedic surgeon, you can be a trauma surgeon, you can do whatever you like.</Blog.Text>
            <Blog.Text>The bottom line is they don't need that - they need reserve specialists. So the military has a lot of reserve specialists, but for full-time doctors they only need GPs. If you are a student thinking about taking up one of the military scholarships and you're not interested in general practice, then dont even look at it! The other thing I would say is that the money is very enticing. They offer you this great salary package and tell you that you'll be debt free - but you should never do anything for money in medicine, because you are signing up with the military and you have to be inclined in that way!</Blog.Text>
            <br />
            <br />
            <Blog.Text><b>The three categories for recruiting prospects</b></Blog.Text>
            <Blog.Text><b> 07:53</b></Blog.Text>
            <Blog.Text><b>Type 1 - People that will never serve in the military:</b> What are you talking about? That's ridiculous. I'm will never be interested in doing that, you have to be crazy to join the military. These are the people that will never be convinced to join.</Blog.Text>
            <Blog.Text><b>Type 2 - Can be convinced to join in the right circumstances:</b> Oh, I haven't really thought about the military, but it sounds like it might be interesting, that could be something I do.</Blog.Text>
            <Blog.Text><b>Type 3 - Born to serve:</b> People with a family history of military service and have always been interested in military culture - that's certainly where I fall into.</Blog.Text>
            <Blog.Text>My grandfather was the Surgeon General for the Australian Navy, he served in World War Two. So I had a family tradition of both medicine and military; growing up I was always interested in military documentaries.</Blog.Text>
            <Blog.Text>I think the people that tend to do the best in the military are the people that have always been interested in it. On the other hand if it's something that you're doing for fun or are not sure about, it's a big deal to take on, and in that case I would think long and hard before going down that road.</Blog.Text>
            <br />
            <br />
            <Blog.Text><b>Reserve service can be a really rewarding</b></Blog.Text>
            <Blog.Text><b>09:13</b></Blog.Text>
            <Blog.Text>An alternative way to engage in military service is through The Reserves. It can still be really rewarding while requiring less commitment. You can do your specialist training and then sign on as a reservist. You only have to give them one weekend per month or a couple of weeks a year. You get tax-free pay, and generally the reservists only get called in times of crisis, which is when most people want to give their service and their time.</Blog.Text>
            <Blog.Text>I've worked with a number of reservists during my full-time service. There's some very senior clinicians: Dr Habersberger, one of the chief cardiologists at the Alfred, is very high up in the Navy reserves.</Blog.Text>
            <Blog.Text><i>There's a surprising number of very senior clinicians in hospitals that do reserve work and it's a great little social network as well. It is a great way to have contacts particularly in the specialties you're interested in!</i></Blog.Text>
            <br />
            <br />
            <Blog.Subtitle>General Practice as a Career</Blog.Subtitle>
            <br />
            <br />
            <Blog.Text><b>The best part being a GP? The longitudinal relationships that you establish and being a part of patients lives</b></Blog.Text>
            <Blog.Text><b>11:55</b></Blog.Text>
            <Blog.Text>The RACGP slogan specialist in life is controversial within GP circles. A lot of GPs hate that term, but I quite like it.</Blog.Text>
            <Blog.Text>I think if you just do the medicine, then you miss out as a GP. I think being involved in patients' lives is the most rewarding part of it.</Blog.Text>
            <Blog.Text>People are thankful for a good GP. You hear a lot of people say: I can't find a good GP, GPs are rubbish. There is a lot of negative sentiment and it's because they haven't found a GP that they have bonded with and has become a part of their lives. When you interview patients with good GPs, they say that GPs are the most important part of their medical care.</Blog.Text>
            <Blog.Text>I have had patients in hospital calling me from their hospital bed wanting my advice, even when they were under specialist care in a hospital environment. </Blog.Text>
            <br />
            <br />
            <Blog.Text><b>It takes a certain type of person to be a GP</b></Blog.Text>
            <Blog.Text><b> 14:32</b></Blog.Text>
            <Blog.Text>You have to be a good communicator.</Blog.Text>
            <Blog.Text>You have to be passionate about preventative health.</Blog.Text>
            <Blog.Text>You have to be want to be involved in people's lives.</Blog.Text>
            <Blog.Text>You have to be interested in a broad spectrum of medicine.</Blog.Text>
            <Blog.Text>You have to know a lot about everything - and so if you're not geared that way, then general practice is not the career for you!</Blog.Text>
            <Blog.Text>If instead you want to be the master of IgA nephropathy and know every nuance there is, or want to be regarded as the person people think of when they are thinking about multifocal atrial tachycardia - then youd be a terrible GP. </Blog.Text>
            <br />
            <br />
            <Blog.Text><b>Being a part of the Community: GPs Down Under</b></Blog.Text>
            <Blog.Text><b>15:27</b></Blog.Text>
            <Blog.Text>I am a member of the Facebook group called GPs Down Under and have been watching the GPs post over the last couple of weeks about responding to the bushfires near their practices. They're setting up little emergency clinics, bringing in drugs, speaking to specialists to learn how they can provide appropriate care when they become isolated when major roads are cut off. People have been turning to their GPs because of respiratory problems with smoke inhalation and the trauma of losing their houses. If being a part of this response is something you would love, then general practice is definitely the career for you.</Blog.Text>
            <br />
            <br />
            <Blog.Text><b>Developing a passion for health education</b></Blog.Text>
            <Blog.Text><b>05:36</b></Blog.Text>
            <Blog.Text>A couple of years after leaving the Navy I started looked for education work, which was part of my military through educating medics in the Navy. So I approached Monash University and worked for the department of General Practice for a number of years and then moved across to the Peninsula clinical school.</Blog.Text>
            <Blog.Text>I've now picked up another job working for Eastern Victoria GP training where I provide educational visits to GP registrars, predominantly supporting the military registrars  providing training advice and clinical visits.</Blog.Text>
            <br />
            <br />
            <Blog.Text><b>10:25</b></Blog.Text>
            <Blog.Text>Some people are naturally more interested in education than others. When I was a medical student I had a job with the faculty where I helped create educational website content, and ended up doing a bit of tutoring, which got harnessed in my teaching role in the military.</Blog.Text>
            <Blog.Text>As a GP I view myself as a health educator: we have to educate our patients about their diseases and their management.</Blog.Text>
            <br />
            <br />
            <Blog.Subtitle>Money and Medicine</Blog.Subtitle>
            <br />
            <br />
            <Blog.Text>Why setting up your finances early should be a priority.</Blog.Text>
            <Blog.Text><b>17:31</b></Blog.Text>
            <Blog.Text>Within medicine it's seems almost taboo to talk about money.</Blog.Text>
            <Blog.Text>The bottom line is that in medicine it doesn't matter what pathway you pick, you're never going to be short of a dollar.</Blog.Text>
            <Blog.Text>The main thing that prompted me to talk about money and medicine, is that I have friends that are specialists earning uber-dollars that are struggling to pay their bills and have debt coming out their eyeballs. They haven't managed their finances properly. I was hoping to instil in this cohort, some financial habits that you can set up early on to help avoid failure later in life.</Blog.Text>
            <Blog.Text>As doctors we get focused on medicine, which is rightfully where our priorities should be. That can leave a lot of other aspects of our lives in a bit of chaos. If you set up your finances early - in terms of your bank accounts, savings plans, super and investment strategies, you will find it easier later on. The last thing you want to be thinking about when you're a cardiothoracics registrar having just done your 30th hour on call:  Oh my God, I'm not sure if I actually set up my loan repayments to come out properly.</Blog.Text>
            <Blog.Text>I've adopted a few financial strategies along the way from Warren Buffett and the Barefoot Investor. I think being interested in money helps you value it more to treat it with the respect it needs, or else it can become another source of stress.</Blog.Text>
            <br />
            <br />
            <Blog.Text><b>Top three financial tips for medical students and junior doctors.</b></Blog.Text>
            <Blog.Text><b>19:47</b></Blog.Text>
            <Blog.Text><b>Tip 1: Set up your super properly.</b></Blog.Text>
            <Blog.Text>It's always better to have more money going into super than you think you need, and to make sure that you're investing in the right style of super.</Blog.Text>
            <Blog.Text>As young junior doctors you are going to be in the workforce for 30 to 40 years, you have the time to ride out the highs and lows of the share market - so put your super into high growth portfolios and make sure that your super is a low-fee super fund.</Blog.Text>
            <Blog.Text><b>Tip 2: Think about a savings plan.</b> As your salary increases, you get hit by what they call lifestyle creep - when your expenses rise with your income.</Blog.Text>
            <Blog.Text>You go from a medical student earning $0, to an intern earning $60,000 to $80,000, then to a consultant ophthalmologist earning $2-3 millions a year. You can find that the consultant ophthalmologist earning $2-3 millions a year has no savings in the bank, as opposed to a junior doctor who has been doing locum HMO work for 10 years who has a million dollars in the bank. The main difference between them is that one has managed not to fall victim to lifestyle creep!</Blog.Text>
            <Blog.Text>Warren Buffett's famous quote is: Spend what is left after saving. Don't save what is left after spending.</Blog.Text>
            <Blog.Text><b>Tip 3: Ensure your insurances and contingencies in place.</b></Blog.Text>
            <Blog.Text>You never know what's around the corner. One of the GPs affected by the bushfire crisis on Kangaroo Island posted about his home being burnt down. His town is shut down, people have evacuated, his clinic is not generating any income and his home is burnt down.</Blog.Text>
            <Blog.Text>You want to make sure that you are prepared for the unexpected.</Blog.Text>
            <Blog.Text>You should always have three months expenses in an easily accessible bank account. Income protection insurance kicks in only after about 10-to-12 weeks, during which youll need to be able to live off your savings. You have to make sure that you have that aside (and that you're not spending it on the latest holiday to the Maldives!)</Blog.Text>
            <br />
            <br />
            <Blog.Text><b>Is investing in the share market risky business?</b></Blog.Text>
            <Blog.Text><b>24:30</b></Blog.Text>
            <Blog.Text>For a lot of people the default is to just put all their savings in the bank. Particularly at the moment with interest rates so low, you would be missing out on so much lost income by doing that. Everyone thinks that the share market is immensely complicated, difficult and risky. But it's not. There's a reason why superannuation funds invest primarly in the ASX 200, which comprises the top 200 companies in Australia - because it is safe. Even if you go with a relatively conservative super portfolio, you will still find that a large chunk of that money will be invested in blue chip Australian shares. If you invest in the ASX 200, through buying some shares in Woolworths, CommBank, Wesfarmers for a 20-to-30 year investment plan - you would find it pretty hard to stuff up!</Blog.Text>
            <Blog.Text>Don't put all your eggs in one basket; it is important to have a good diversified portfolio.</Blog.Text>
            <Blog.Text>Occasionally disasters happen, but if it is only 2% of your portfolio, you can endure it.</Blog.Text>
            <br />
            <br />
            <Blog.Subtitle>Should medical doctors have secondary roles?</Blog.Subtitle>
            <br />
            <br />
            <Blog.Text><b>Medicine and Politics</b></Blog.Text>
            <Blog.Text><b>32:25</b></Blog.Text>
            <Blog.Text>There's a lot of doctors who are politically minded and there are loads of opportunities for that. For example Richard Di Natale is a doctor who felt that he would better serve the country as a politician.</Blog.Text>
            <Blog.Text>The RACGP never used to have anything resembling a political presence, but lately it's becoming more of a political organisation. I think doctors are a powerful force if we want to be. The problem is that we're a hard group to coordinate. Even on issues like climate change where we have an unanimous position (both AMA and RACGP have come out with statements) and special interest groups such as Doctors for the Environment, we are not really prominent or influential. We are a hard group to coordinate because our focus is always on patients - that's where it should be. Patient outcomes come first , but it doesn't mean that we can't have these secondary roles.</Blog.Text>
            <Blog.Text>I think campaigning for change that were passionate about is important for us. We are in the position that we have exposure to the general public.</Blog.Text>
            <Blog.Text>I talk about climate change a lot with my patients. They say that the golden rule is Don't talk politics with your patients. I do avoid politics in general, but I think when there's issues such as climate change that are affecting health, we should be advocates in that space. Definitely make your voices heard - junior doctors in particular. We're seeing that the horrendous bullying of junior doctors that has happened in the past is gradually changing, and the only people able to generate change is junior doctors speaking out. So dont be silent. As a collective voice we can be powerful, as well as individually.</Blog.Text>
            <br />
            <br />
            <Blog.Text><b>39:09</b></Blog.Text>
            <Blog.Text>My wife always laughs because I'm an advocate of the sternly worded letter as I love to write to politicians. I love to complain about things that need to be changed. People always think that it doesn't accomplish anything, but every time I write to a politician I always get a response. It might not be the response you want, but at least it's flagging issues and they become aware of what challenges are faced. At the moment it seems like politicians are very out of step with a lot of the stuff that we're doing, particularly on climate change - so be vocal, be passionate and take it personally!</Blog.Text>
            <br />
            <br />
            <Blog.Text><b>Medicine and the Media</b></Blog.Text>
            <Blog.Text><b>35:36</b></Blog.Text>
            <Blog.Text>There are doctors using media to generate change.</Blog.Text>
            <Blog.Text>One of the things I did for a while was being Dr Kiss for the Kiss FM radio. I did it when Matt and Michelle were on the radio show and they used to occasionally call me up when they had medical questions. So you can find all sorts of weird spots to get involved with that are non-medical. Sally Cockburn, the Doctor Feel Good also had a prominent media profile for a while.</Blog.Text>
            <br />
            <br />
            <Blog.Text>Final words of advice</Blog.Text>
            <Blog.Text><b>44:46</b></Blog.Text>
            <Blog.Text><b>Personal philosophy:</b> Family and community come first.</Blog.Text>
            <Blog.Text><b>Advice for medical students and junior doctors:</b> Be as open-minded as you possibly can to every single career. The way that med school is geared now, makes you feel like you have to start first year med school knowing that Im going to be a neurosurgeon, because I need to get five PhDs before Im even considered for the program - but that approach just gives you tunnel vision. When you get to middle age, your life priorities start to change so I think keeping an open mind all the way through your career is really important.</Blog.Text>
            <br />
            <br />
        </Blog.TextBlock>
    )
}