import React from 'react';
import { Blog } from '../../components';

export default function steve_trumble() {
    return (
        <Blog.TextBlock>
            <Blog.Text>Back in the day I never wanted to be a doctor. I wanted to be a vet, and I missed out by one pointI loved the old James Herriot books and I love dogs. But Im so glad that I did go to medicine. I became more attuned to it as I went, and its something Ive never regretted not for one moment.</Blog.Text>
            <br/>
            <br/>
            <Blog.Subtitle>INTERNSHIP WAR STORIES </Blog.Subtitle>
            <Blog.Text>I did my internship at Geelong. Back then that was a hotly contested internship if you wanted to be a GP: they had a really good GP training program, and it was a big hospital that could take care of people with all sorts of common problems.</Blog.Text>
            <Blog.Text>I remember weeping as an intern, just with sheer exhaustion. This is where your listeners will be rolling their eyes as he tells his war story. (puts on his best voiceover for comedic effect) Back in the day (reverts to his normal voice) when you were on call for Dr. Barretts medical unit in Geelong, youd pick up your beeper at 8 oclock on the Friday morning to start receiving medical patients, and you would hand it in on Tuesday at 6pm, not having being off duty from Friday morning to Tuesday evening. That was just brutal.</Blog.Text>
            <Blog.Text>Those were the days of being blooded as an intern - if you cant hack it, get out - that sort of thing. Im so proud of your generation and I wish ours had the guts to do it. To stand up and say - This is not appropriate. This is not the way Ill practice. This is no good for our patients. Its like being drunk at work to be that tired; to be weeping just with the sheer desperation of having to admit another patient.</Blog.Text>
            <br/>
            <br/>
            <Blog.Subtitle>INTERNSHIP PLEASURES </Blog.Subtitle>
            <Blog.Text>The good days were living in Ocean Grove as an intern, buying one of the first windsurfers in Australia, and pounding across the waters of Lake Connewarre on an afternoon off just because I could.</Blog.Text>
            <Blog.Text>Or getting dressed up and going out to spend our first paycheck in one of the best restaurants in Geelong. Youve got to promise me that you will do that! Thats tradition. Well it is now.</Blog.Text>
            <br/>
            <br/>
        </Blog.TextBlock>
    )
}